@import "abstracts/functions";
@import "abstracts/mixins";
@import "abstracts/variables";

@import "base/reset";
@import "base/typography";
@import "base/base";

@import "includes/header";
@import "includes/footer";

@import "components/loader";
@import "components/popup";

body,
html {
  position: relative;
  overflow-x: hidden;
  background-color: #073818;
  color: #fff;
  font-family: 'Gotham Pro', sans-serif;
}

body {
  background-image: url('../img/back.svg');
  background-position: center 88px;
  background-repeat: no-repeat;
  background-size: initial;
}

.caption {
  text-align: center;
  font-weight: bold;
  font-size: 28px;
  line-height: 120%;
  text-transform: uppercase;
}

.left-top-tag {
  position: absolute;
  left: 0;
  top: 0;
  z-index: 1;
}

.header {
  margin: 30px auto 0;
  text-align: center;
  padding-bottom: 64px;

  &__logo {
    display: block;
    margin-bottom: 41px;
    margin-left: auto;
    margin-right: auto;
  }

  &__caption {
    text-transform: uppercase;
    font-size: 32px;
    line-height: 100%;
    font-weight: 400;
    margin-bottom: 59px;

    span {
      font-weight: bold;
      display: block;
      width: fit-content;
      margin-left: auto;
      margin-right: auto;
      margin-bottom: 10px;
      background: -webkit-linear-gradient(180deg, #B58840, #F2E996, #B58840);
      -webkit-background-clip: text;
      -webkit-text-fill-color: transparent;
    }
  }

  &__jars {
    display: flex;
    justify-content: center;
    align-items: center;

    &_item {
      position: relative;

      &.right {

        .header__jars_about {
          top: 113px;
          left: calc(100% + 17px);

          &-title {

            &:before {
              content: '**';
            }
          }
        }

        .header__jars_img {
          margin-right: -53px;
        }
      }

      &.left {

        .header__jars_about {
          right: 100%;
          top: 15px;

          &-title {

            &:before {
              content: '*';
            }
          }
        }
      }
    }

    &_about {
      position: absolute;
      width: 320px;
      text-align: center;

      &-bg {
        position: absolute;
        left: 50%;
        top: -100px;
        transform: translateX(-50%);
        z-index: 1;
      }

      &-title {
        font-size: 25px;
        line-height: 32px;
        margin-bottom: 14px;
        position: relative;
        z-index: 2;
        background: -webkit-linear-gradient(180deg, #B58840, #F2E996, #B58840);
      -webkit-background-clip: text;
      -webkit-text-fill-color: transparent;
        width: fit-content;
        margin-left: auto;
        margin-right: auto;

        &:before {
          position: absolute;
          top: 0;
          color: #fff;
          left: 100%;
          z-index: 2;
          font-size: 20px;
        }
      }

      &-subtitle {
        font-size: 21px;
        line-height: 130%;
        font-style: italic;
        position: relative;
        z-index: 2;
        margin-bottom: 56px;
        background: -webkit-linear-gradient(180deg, #B58840, #F2E996, #B58840);
      -webkit-background-clip: text;
      -webkit-text-fill-color: transparent;

        &:before {
          content: '';
          position: absolute;
          left: 50%;
          transform: translateX(-50%);
          background-image: url('../img/decor.svg');
          background-repeat: no-repeat;
          background-position: center;
          background-size: initial;
          bottom: -35px;
          width: 26px;
          height: 15px;
          display: block;
        }
      }

      &-text {
        font-size: 16px;
        line-height: 130%;
        color: #E6CFA0;
        position: relative;
        z-index: 1;
      }
    }
  }
}

.main {
  max-width: 1000px;
  margin-left: auto;
  margin-right: auto;
  width: 100%;

  .recipes {
    position: relative;
    margin-top: 50px;
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    align-items: center;
    margin-bottom: 33px;

    &__bg {
      position: absolute;
      z-index: 0;
      left: 50%;
      top: 50%;
      transform: translate(-50%, -50%);
    }

    &__item {
      position: relative;
      z-index: 1;
      width: 300px;
      height: 340px;
      display: flex;
      flex-direction: column;
      align-items: center;
      justify-content: flex-end;
      margin-bottom: 50px;
      background-color: #073818;
      border: 2px solid #D4B464;
      border-radius: 15px;
      padding-bottom: 30px;
      padding-left: 10px;
      padding-right: 10px;
      background-image: url('../img/riceps_item-bg.svg');
      background-position: center -12px;
      background-size: initial;
      background-repeat: no-repeat;
      cursor: pointer;
      @include transition-ease;
      box-shadow: 0px 0px 0px #02240E;

      &:hover {
        transform: scale(1.02);
        box-shadow: 0px 0px 25px #02240E;
      }

      &:before,
      &:after {
        position: absolute;
        z-index: 2;
        font-weight: bold;
        font-size: 14px;
        line-height: 24px;
        text-transform: uppercase;
        width: 135px;
        height: 30px;
        text-align: center;
        padding-top: 3px;
        box-sizing: border-box;
        left: -14px;
        top: 20px;
        background-size: contain;
        background-position: center;
        background-repeat: no-repeat;
      }

      &:nth-of-type(3):after,
      &:nth-of-type(4):after,
      &:nth-of-type(5):after,
      &:nth-of-type(6):after {
        content: attr(data-caramel);
        background-image: url('../img/caramel-tag-bg.svg');
      }

      &:nth-of-type(5):after,
      &:nth-of-type(6):after {
        top: 60px;
      }

      &:before {
        content: attr(data-nut);
        background-image: url('../img/nut-tag-bg.svg');
      }

      &_title {
        color: #D4B464;
        text-transform: uppercase;
        font-weight: bold;
        font-size: 16px;
        line-height: 110%;
        text-align: center;
        height: 36px;
        margin-top: 20px;
      }
    }
  }

  .shops {
    display: flex;
    justify-content: space-between;
    align-items: center;
    flex-wrap: wrap;
    margin-top: 43px;

    &__item {
      margin-bottom: 23px;
    }
  }
}


